import React from 'react'
import { Link } from 'react-router-dom'

import Button from '../Button/Button'

const TableActionButtons = ({handleDelete, id}) => {
  return (
    <div
      className="btn-group btn-group-sm"
      role="group"
      aria-label="Actions"
    > 
      <Link to={`./edit/${id}`}>
        <Button 
          color="secondary"
          label="Edit"
          icon="pencil"
        />
      </Link>
      <Button
        id={id}
        color="danger"
        label="Remove"
        icon="trash-o"
        handleClick={handleDelete}
      />
    </div>
  )
}

export default TableActionButtons