import React from 'react'

const SelectInput = ({error, selected, value, values, handleChange, label, name, required}) => {
  let isInvalid = error  && error.length > 0 ? 'is-invalid' : ''

  return (
    <>
      <label htmlFor={name}>{label}
      {required &&
        <span className="text-primary">*</span>}
      </label>
      <select defaultValue={value} name={name} className={`form-control ${isInvalid}`} value={selected} onChange={(e) => handleChange(e)}>
        <option value="" disabled>Select...</option>
        {values.map((option, i)=> {
          return (
            <option data-testid="option" key={i} value={option}>{option}</option>
          )
        })}
      </select>
      {isInvalid &&
        <span className='invalid-feedback'>{error}</span>}
    </>
  )
}

export default SelectInput