import React from 'react'

const Pagination = ({handleClick, currentPageNumber, totalItems, pageLimit}) => {
  const pageNumbers = []
  for (let i = 1; i <= Math.ceil(totalItems / pageLimit); i++) {
    pageNumbers.push(i)
  }

  let disabledPrev = currentPageNumber <= 1 && 'disabled'
  let disabledNext = currentPageNumber === pageNumbers.length  && 'disabled'

  return (
    <nav aria-label="Data grid navigation">
      <ul className="pagination justify-content-end">
        <li className={`page-item ${disabledPrev}`}>
          <button type="button" className="page-link" onClick={() => handleClick(currentPageNumber - 1)}>
            Previous
          </button>
        </li>
        {pageNumbers.map((item, i) => { 
          let active = item === currentPageNumber && 'active'
          return (
          <li key={i} className={`page-item ${active}`}>
            <button type="button" className="page-link" onClick={() => handleClick(item)}>
              {item} <span className="sr-only">(current)</span>
            </button>
          </li>
        )})}
        <li className={`page-item ${disabledNext}`}>
          <button type="button" className="page-link" onClick={() => handleClick(currentPageNumber + 1)}>
            Next
          </button>
        </li>
      </ul>
    </nav>
  )
}

export default Pagination