import React from 'react'

import Container from '../Container/Container'

const FormRow = ({children, label, required, error}) => {
  let isInvalid = error  && error.length > 0 ? 'is-invalid' : ''

  return (
      <Container>
        <Container className={`${isInvalid}`}>{label}
          {required && 
            <span className="text-primary">*</span>}
        </Container>
        {children}
        {isInvalid &&
          <span className='invalid-feedback'>{error}</span>}
      </Container>
  )
}

export default FormRow