import React, { Component } from 'react'
import { Route } from 'react-router-dom'

import ListView from './app/list-view/ListView'
import CharacterFormView from './app/character-form-view/CharacterFormView'

import Container from './common/components/Container/Container'
import NavigationBar from './common/components/NavigationBar/NavigationBar'

class App extends Component {
  render() {
    return (
      <Container className="App">
        <NavigationBar label="ReactTestApp" />
        <Container className="container">
          <Route exact path="/" component={ListView} />
          <Route exact path="/add" render={(props) => <CharacterFormView {...props} isEdit={false} />}/>
          <Route exact path="/edit/:id" render={(props) => <CharacterFormView {...props} id={props.match.params.id} isEdit={true}/>} />
        </Container>
      </Container>
    )
  }
}

export default App
