import React, { Fragment, Component } from 'react'
import { Link } from 'react-router-dom'

import ListTable from './components/ListTable'
import Title from  '../../common/components/Title/Title'
import Container from '../../common/components/Container/Container'
import SearchBar from '../../common/components/SearchBar/SearchBar'
import RowContainer from '../../common/components/RowContainer/RowContainer'
import Button from '../../common/components/Button/Button'
import Pagination from '../../common/components/Pagination/Pagination'
import Loading from '../../common/components/Loading/Loading'

import { GET, SEARCH, DELETE, GET_WITH_PAGINATION } from '../../common/hooks/api'

class ListView extends Component {
  constructor(props) {
    super(props)

    this.state = {
      loading: true,
      characters: [],
      charactersLength: 0,
      tabelHeaders: ['Id', 'Name', 'Species', 'Gender', 'Homeworld', 'Actions'],
      currentPageNumber: 1,
      totalItems: 1,
      pageLimit: 10
    }

    this.handleClick = this.handleClick.bind(this)
    this.handleChange = this.handleChange.bind(this)
    this.handleDelete = this.handleDelete.bind(this)
  }

  totalCount() {
    GET('characters').then(res => {
      this.setState(state => {
        state.totalItems = res.length
        return state
      })
    })
  }

  getSearchQuery(query) {
    let self = this
    setTimeout(function() {
      SEARCH('characters', query).then(res => {
        self.setState(state => {
          state.characters = res
          return state
        })
      })
    }, 200)
  }

  getCharacters(pageNumber) {
    GET_WITH_PAGINATION('characters', pageNumber)
      .then(res => {
        this.setState(state => {
          state.characters = res
          state.charactersLength = res.length
          state.currentPageNumber = pageNumber
          state.loading = false
          return state
        })
      })
      .catch(err => console.error('err', err))
  }

  componentDidMount() {
    this.getCharacters(1)
    this.totalCount()
  }

  handleClick(pageNumber) {
    this.getCharacters(pageNumber)
  }

  handleChange(event) {
    event.preventDefault()
    const { value } = event.target
    this.getSearchQuery(value)
  }

  handleDelete(event, id) {
    event.preventDefault()
    DELETE('characters', id)
      .then((res) => res.status === 200 && (
        this.setState({totalItems: this.state.totalItems - 1}),
        this.getCharacters(this.state.totalItems % this.state.pageLimit === 0 ? this.state.currentPageNumber - 1 : this.state.currentPageNumber) )
      )
      .catch(err => console.error('err', err))
  }

  render() {
    if (this.state.loading) {
      return <Loading content="Loading..."/>
    } else {
      return (
        <Fragment>
          <Title text="List View" />

          <RowContainer>
            <Container className="col-sm-6">
                <SearchBar 
                  handleChange={this.handleChange}
                />
            </Container>
            <Container className="col-sm-6 text-sm-right">
              <Link to="/add">
                <Button
                  label="Add New"
                  color="primary"
                  marginBottom="mb-3"
                />
              </Link>
            </Container>
          </RowContainer>

          <ListTable
            headers={this.state.tabelHeaders}
            data={this.state.characters}
            handleDelete={this.handleDelete}
          />

          <Pagination 
            handleClick={this.handleClick}
            currentPageNumber={this.state.currentPageNumber}
            totalItems={this.state.totalItems}
            pageLimit={this.state.pageLimit}
          />
        </Fragment>
      )
    }
  }
}

export default ListView
