import React, { Fragment, Component } from 'react'

import Title from  '../../common/components/Title/Title'
import Form from '../../common/components/Form/Form'

import { GET, PUT, POST, GET_BY_ID  } from '../../common/hooks/api'

import Container from '../../common/components/Container/Container'
import FormRow from '../../common/components/Form/FormRow'
import FormInput from '../../common/components/Form/FormInput'
import FormRadioButtonGroups from '../../common/components/Form/FormRadioButtonGroup'
import Button from '../../common/components/Button/Button'
import RadioButton from '../../common/components/RadioButton/RadioButton'
import SelectInput from '../../common/components/SelectInput/SelectInput'
import Loading from '../../common/components/Loading/Loading'

class CharacterFormView extends Component {
  constructor(props) {
    super(props)

    this.state = {
      disableSubbmit: false,
      loading: true,
      id: this.props.id,
      isEdit: this.props.isEdit,
      species: [],
      selectedSpecies: '',
      gender: '',
      name: '',
      homeworld: '',
      errors: {
        name: '',
        selectedSpecies: '',
        gender: ''
      },
      touched: {
        name: false
      },
    }

    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }

  getSpecies() {
    GET('species')
      .then(res => {
        this.setState(state => {
          state.species = res
          return state
        })
      })
      .catch(err => console.error('err', err))
  }

  getCharacterData() {
    GET_BY_ID('characters', this.state.id)
      .then(res => {
        this.setState(state => {
          state.name = res.name
          state.selectedSpecies = res.species
          state.gender = res.gender
          state.homeworld = res.homeworld
          state.loading = false
          return state
        })
      })
      .catch(err => console.error('err', err))
  }

  handleChangeValidation(name, value) {
    let errors = this.state.errors

    switch (name) {
      case 'name':
        errors.name =
          value.length === 0
            ? 'This Field is Required '
            : ''
        break
      case 'gender':
        errors.gender =
          value.length === 0
            ? 'This Field is Required '
            : ''
        break
      case 'selectedSpecies':
        errors.selectedSpecies =
          value.length === 0
            ? 'This Field is Required '
            : ''
        break
      default:
        break
    }
  }

  handleSubmitValidation() {
    let fields = this.state
    let errors = {}
    let formIsValid = true

    if(!fields['name']) {
      formIsValid = false
      errors['name'] = ' This field is required'
    }

    if(!fields['selectedSpecies']) {
      formIsValid = false
      errors['selectedSpecies'] = ' This field is required'
    }

    if(!fields['gender']) {
      formIsValid = false
      errors['gender'] = ' This field is required'
    }

    this.setState({errors: errors})

    return formIsValid
  }

  handleSubmit(event) {
    event.preventDefault()
    this.setState({ disableSubbmit: true })

    if(this.handleSubmitValidation()) {
      if(this.state.isEdit) {
        PUT('characters', {
          name: this.state.name,
          species: this.state.selectedSpecies,
          gender: this.state.gender,
          homeworld: this.state.homeworld
        }, this.state.id).then(()=> {
          this.props.history.push('/')
        })
      } else {
        POST('characters', {
          name: this.state.name,
          species: this.state.selectedSpecies,
          gender: this.state.gender,
          homeworld: this.state.homeworld
        }).then(()=> {
          this.props.history.push('/')
        })
      }
    } else {
      this.setState({ disableSubbmit: true })
    }
  }

  handleChange(event) {
    const { name, value } = event.target

    this.handleChangeValidation(name, value)
    Object.values(this.state.errors).every(item => (item === '')) ? this.setState({ disableSubbmit: false }) : this.setState({ disableSubbmit: true })
    this.setState({[name]: value})
  }

  componentDidMount() {
    this.getSpecies()
    this.state.isEdit ? this.getCharacterData() : this.setState({loading: false})
  }


  render() {
    if (this.state.loading) {
      return <Loading content="Loading..."/>
    } else {
      return (
        <Fragment>
          <Title text={this.state.isEdit ? 'Edit Character' : 'Add New Character'} />

          <Form
            handleSubmit={this.handleSubmit}
          >
            <FormRow>
              <Container className="col-md-4 mb-3">
                <FormInput
                  label="Name"
                  name="name"
                  value={this.state.name}
                  handleChange={this.handleChange}
                  error={this.state.errors.name}
                  required={true}
                />
              </Container>
            </FormRow>
            <FormRow>
              <Container className="col-md-4 mb-3">
                <SelectInput
                  label="Species"
                  name="selectedSpecies"
                  values={this.state.species}
                  value={this.state.selectedSpecies}
                  handleChange={this.handleChange}
                  error={this.state.errors.selectedSpecies}
                  required={true}
                />
              </Container>
            </FormRow>
            <FormRow>
              <Container className="col-md-4 mb-3">
                <FormRadioButtonGroups
                  label="Gender"
                  required={true}
                  error={this.state.errors.gender}
                >
                  <RadioButton
                    name="gender"
                    value="male"
                    label="Male"
                    handleChange={this.handleChange}
                    checked={this.state.gender === 'male'}
                  />
                  <RadioButton
                    name="gender"
                    value="female"
                    label="Female"
                    handleChange={this.handleChange}
                    checked={this.state.gender === 'female'}
                  />
                  <RadioButton
                    name="gender"
                    value="n/a"
                    label="n/a"
                    handleChange={this.handleChange}
                    checked={this.state.gender === 'n/a'}
                  />
                </FormRadioButtonGroups>
              </Container>
            </FormRow>
            <FormRow>
              <Container className="col-md-4 mb-3">
                <FormInput
                  label="Homeworld"
                  name="homeworld"
                  handleChange={this.handleChange}
                  value={this.state.homeworld}
                />
              </Container>
            </FormRow>
            <FormRow>
              <Container className="col-md-4 mb-3">
                <Button
                  color="primary"
                  type="submit"
                  label="Submit"
                  disabled={this.state.disableSubbmit}
                />
              </Container>
            </FormRow>
          </Form>
        </Fragment>
      )
    }
  }
}

export default CharacterFormView
